<template>
  <div class="header">
    <div class="header-wrapper">

      <div class="top-nav">
        <div class="top-nav-wrapper" :class="{isLogin: isLogin }">
          <div class="link-buttons">
            <ul>
              <li
                v-for="(topItem, topKey) in topNavButtons"
                :key="topKey"
                :class="{activeTopItem: activeTopButton == topKey}"
                @click="goPage(topItem.link)">
                <img
                  :class="'icon-'+topKey"
                  width="14"
                  height="22"
                  :src="require('../../assets/image/home/'+ topItem.Icon +'.png')"
                  alt="">
                <span>{{ topItem.title }}</span>
              </li>
            </ul>
          </div>
          <div class="action-buttons">
            <div v-if="!isLogin">
              <Button type="success" @click="goPage('Login')">登录</Button>
              <Button type="info" @click="goPage('Register')">注册</Button>
            </div>
            <div v-else class="login-text">
              <img 
                @click="goPage('MyCenter')"
                width="30"
                height="30"
                src="../../assets/image/header/portrait.png" 
                alt="">
              <span @click="goPage('MyCenter')">同头是岸</span>
            </div>
          </div>
        </div>
      </div>

      <div class="bottom-nav">
        <div class="bottom-nav-wrapper">
          <div class="link-buttons">
            <ul>
              <li
                v-for="(botItem, botKey) in botNavButtons"
                :key="botKey"
                :class="{activeTopItem: activeTopButton == botKey}">
                <span @click="goPage(botItem.link)">{{ botItem.title }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>

    </div>
    <Tutor
      :showTutor="showTutorModal"
      @closeTutor="closeTutor($event)"/>
  </div>
</template>

<script>
import Tutor from '@/components/Tutor'
export default {
  name: 'Header',
  components: {
    Tutor,
  },
  data() {
    return {
      topNavButtons: [
        { title: '手机版', link: '', Icon: 'icon_handset' },
        { title: '戒友论坛', link: 'Forum', Icon: 'icon_forum' },
        { title: '心事树洞', link: 'TheeHole', Icon: 'icon_Treehole' },
        { title: '聆听者', link: 'Tutor', Icon: 'icon_Listeners' },
      ],
      botNavButtons: [
        { title: '首页', link: 'Home' },
        { title: '戒友圈', link: 'Forum' },
        { title: '每日签到', link: 'DailyCheckIn' },
        { title: '戒赌', link: 'QuitGambling' },
        { title: '债务', link: 'Debt' },
        { title: '导航', link: 'Navigation' },
      ],
      showTutorModal: false
    };
  },
  props: {
    activeTopButton: {
      type: Number,
      default: 0
    }
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    }
  },
  methods: {
    goPage(url) {
      if (this.$route.name == url) return;
      else {
        if (url != 'Tutor') {
          this.$router.push({ name: url });
        } else {
          this.showTutor();
        }
      }
    },
    login() {
      this.$store.dispatch("setLoginStatus", true)
    },
    showTutor() {
      this.showTutorModal = true;
    },
    closeTutor() {
      this.showTutorModal = false;
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
@import "../../assets/css/vars.less";
@import "../../assets/css/common/header.less";
</style>
